import React from 'react';
import axios from 'axios';
import { Spin, PageHeader, Button, Card, Row, Col, Space, Descriptions, Carousel } from 'antd';

import { UserContext } from '../../contexts';
import { UseContext } from '../../hoc';
import { alternatorApi } from '../../config';
import { UiRestriction } from '../../components';
import { alternatorlist as cms } from '../../cms';

const { Grid } = Card,
  { Item } = Descriptions;

const cardDetailsMapping = {
  part: [
    {
      key: 'ampLabel',
      value: 'amp',
    }, {
      key: 'connectionsLabel',
      value: 'connections',
    }, {
      key: 'fanTypeLabel',
      value: 'fanPulleyType',
    }, {
      key: 'fixingPointLabel',
      value: 'fixingPoint',
    }
  ],
  vehicle: [
    {
      key: 'fuelTypeLabel',
      value: 'fuelType',
    }, {
      key: 'transmissionLabel',
      value: 'transmissionType',
    }, {
      key: 'startLabel',
      value: 'startYear',
    }, {
      key: 'endLabel',
      value: 'endYear',
    }, {
      key: 'makeLabel',
      value: 'make',
    }, {
      key: 'modelLabel',
      value: 'model',
    }, {
      key: 'engineCodeLabel',
      value: 'engineCodes',
    }
  ],
}

class AlternatorListing extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: null,
      loading: true,
      error: null,
    }
  }

  async componentDidMount() {
    try {
      const { location, match: { params } } = this.props;
      if (params) {
        console.log(":::", location);
        const { data } = await axios.get(`${alternatorApi.get}/${params.id}`);
        this.setState({ loading: false, data });
      } else {
        this.setState({ loading: false, error: true });
      }
    } catch (err) {
      this.setState({ loading: false, error: err })
    }
  }

  handleAdd = () => {
    const { history } = this.props;
    history.push('/alternators/add')
  }

  displayDescriptionItems = (config, data) => {
    return config.map(({ key, value }) => (
      <Item key={key} label={cms[key]}>
        {Array.isArray(data[value]) ? data[value].join(',') : data[value]}
      </Item>
    ))
  }

  render() {
    const { loading, error, data } = this.state;
    console.log("::::", loading, error, data)
    if (loading) {
      return (
        <div className="example" style={{ position: 'absolute', left: '50%', top: '50%' }}>
          <Spin size="large" />
        </div>
      )
    }

    if (error) {
      return (
        <>Some Error Occured</>
      )
    }

    return (
      <div>
        <PageHeader
          ghost={false}
          onBack={() => this.props.history.push('/alternators')}
          title={data.name}
        // subTitle="This is a subtitle"
        // extra={[
        //   <Button key="3">Operation</Button>,
        //   <Button key="2">Operation</Button>,
        //   <Button key="1" type="primary">
        //     Primary
        //   </Button>,
        // ]}
        >
          <Row gutter={16}>
            <Col span={24}>

              <Space direction="vertical" size="large" >
                <Card
                  size="middle"
                  key={data._id}
                >
                  <Row gutter={16}>
                    <Col xl={8} lg={6} md={12} sm={24} xs={24}>
                      <Row>
                        <Col span={24}>{data.code}</Col>
                        <Col span={24}>{data.name}</Col>

                        <Col span={24}>
                          <Carousel
                            autoplay
                            style={{ height: 300, maxWidth: 490 }}
                          >
                            {data.imgUrl.map((url, i) => <img key={i} src={url} />)}
                          </Carousel>
                        </Col>
                      </Row>
                    </Col>
                    <Col xl={8} lg={9} md={12} sm={24} xs={24}>
                      <Descriptions
                        column={1}
                        bordered
                        title="Part Details"
                      >
                        {this.displayDescriptionItems(cardDetailsMapping.part, data)}
                      </Descriptions>
                    </Col>
                    <Col xl={8} lg={9} md={24} sm={24} xs={24}>
                      <Descriptions
                        column={1}
                        bordered
                        title="Car Details"
                      >
                        {this.displayDescriptionItems(cardDetailsMapping.vehicle, data)}
                      </Descriptions>
                    </Col>
                  </Row>
                  {/* <Grid style={{ width: '60%' }}>
                    
                  </Grid>
                  <Grid style={{ width: '60%' }}>

                  </Grid> */}
                </Card>
              </Space>
            </Col>
          </Row>
        </PageHeader>
      </div>
    )
  }
}

export default UseContext(UserContext)(AlternatorListing);